import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom'; 

import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import classes from './Auth.css';
import * as actions from '../../store/actions/index';
import Spinner from '../../components/UI/Spinner/Spinner';
import { updateObject, checkValidity } from '../../shared/utility';

class Auth extends Component {
    state = {
        controls: {
            email: {
                elementType: 'input',
                elementConfig: {
                    type: 'email',
                    placeholder: 'Email Address'
                },
                value: '',
                validation: {
                    required: true,
                    isEmail: true
                },
                valid: false,
                touched: false,
                errorMessage: 'This field should contain valid email!'
            },
            password: {
                elementType: 'input',
                elementConfig: {
                    type: 'password',
                    placeholder: 'Password'
                },
                value: '',
                validation: {
                    required: true,
                    minLength: 6
                },
                valid: false,
                touched: false,
                errorMessage: 'Password must be at least 6 characters!'
            }
        },
        isSignup: true
    }

    componentDidMount () {
        if (!this.props.burgerBuilding && this.props.authRedirectPath !== '/') {
            this.props.onSetAuthRedirectPath('/');
        }
    }

    inputChangedHandler = (event, inputElementName) => {
        const updatedControls = updateObject(this.state.controls, {
            [inputElementName]: updateObject(this.state.controls[inputElementName], {
                value: event.target.value,
                valid: checkValidity(event.target.value, this.state.controls[inputElementName].validation),
                touched: true
            })
        });
        this.setState({controls: updatedControls});
    }

    submitHandler = (event) => {
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value, this.state.controls.password.value, this.state.isSignup);
    }

    switchAuthModeHandler = () => {
        this.setState(prevState => {
            return {isSignup: !prevState.isSignup};
        });
    }

    render () {
        let inputElementsArray = [];
        for (let key in this.state.controls) {
            inputElementsArray.push({
                id: key,
                config: this.state.controls[key]
            })
        }

        let form = inputElementsArray.map(inputElement => (
            <Input
                key={inputElement.id} 
                elementType={inputElement.config.elementType} 
                elementConfig={inputElement.config.elementConfig} 
                value={inputElement.config.value}
                invalid={!inputElement.config.valid}
                shouldValidate={inputElement.config.validation}
                touched={inputElement.config.touched}
                errorMessage={inputElement.config.errorMessage}
                changed={(event) => this.inputChangedHandler(event, inputElement.id)} />
        ));

        if (this.props.loading) {
            form = <Spinner />
        }

        let errorMessage = null;
        if (this.props.error) {
            let message = null;
            
            switch (this.props.error.message) {
                case 'INVALID_EMAIL':
                    message = 'The email address is invalid!';
                    break;
                case 'EMAIL_EXISTS':
                    message = 'The email address is already in use by another account.';
                    break;
                case 'OPERATION_NOT_ALLOWED':
                    message = 'Password sign-in is disabled for this project.';
                    break;
                case 'TOO_MANY_ATTEMPTS_TRY_LATER':
                    message = 'We have blocked all requests from this device due to unusual activity. Try again later!';
                    break;
                case 'EMAIL_NOT_FOUND':
                    message = 'There is no user record corresponding to this identifier. The user may have been deleted.';
                    break;
                case 'INVALID_PASSWORD':
                    message = 'The password is invalid or the user does not have a password.';
                    break;
                case 'USER_DISABLED':
                    message = 'The user account has been disabled by an administrator';
                    break;
                default:
                    message = 'An error has occured!';
                    break;
            }

            errorMessage = (<p style={{color: 'red'}}>{message}</p>);
        }

        let isAuthenticated = null;
        if (this.props.isAuthenticated) {
            isAuthenticated = <Redirect to={this.props.authRedirectPath} />
        }

        return (
            <div className={classes.Auth}>
                {isAuthenticated}
                <h4>{this.state.isSignup ? 'Sign up': 'Sign in'}</h4>
                <form onSubmit={this.submitHandler}>
                    {form}
                    {errorMessage}
                    <Button btnType='Success'>Submit</Button>
                </form>
                <Button btnType='Danger' clicked={this.switchAuthModeHandler}>SWITCH TO {this.state.isSignup ? 'SIGNIN': 'SIGNUP'}</Button>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        loading: state.auth.loading,
        error: state.auth.error,
        isAuthenticated: state.auth.idToken !== null,
        authRedirectPath: state.auth.authRedirectPath,
        burgerBuilding: state.burgerBuilder.building
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (email, password, isSignup) => dispatch(actions.auth(email, password, isSignup)),
        onSetAuthRedirectPath: (path) => dispatch(actions.setAuthRedirectPath(path))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth);


